.contenedor-menu {
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: blue; }
  .contenedor-menu .header-menu {
    height: 47px;
    padding: 18px 20px 0;
    color: #ffffff;
    display: flex;
    justify-content: space-between; }
    .contenedor-menu .header-menu #logo-menu {
      width: 92px;
      display: inline-block; }
      .contenedor-menu .header-menu #logo-menu .div-logo-video {
        transform: none; }
        .contenedor-menu .header-menu #logo-menu .div-logo-video #logo #Capa_1 {
          fill: white; }
    .contenedor-menu .header-menu #cerrar-menu {
      display: inline-block;
      text-align: right;
      width: 30px; }
  .contenedor-menu .rutas-menu {
    margin-top: 193px;
    text-align: center;
    font-size: 60px;
    line-height: normal;
    letter-spacing: normal; }
    .contenedor-menu .rutas-menu li {
      list-style: none; }
      .contenedor-menu .rutas-menu li a {
        text-decoration: none;
        color: #14373f; }
      .contenedor-menu .rutas-menu li a:hover {
        color: #FFFFFF; }
  .contenedor-menu .footer-menu {
    bottom: 20px;
    position: absolute;
    margin: 0 36px 26px 0;
    right: 0; }
    .contenedor-menu .footer-menu span a {
      width: 61px;
      height: 25px;
      font-size: 24px;
      font-weight: normal;
      text-decoration: none;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff; }

.oculto {
  display: none; }

.visible {
  display: block; }
